import { Component, OnInit } from '@angular/core';
import { GoogleChartInterface } from 'ng2-google-charts';
import { TravelService } from '../../services/travel.service';
import { TripotoPost } from '../../models/tripoto-post';

@Component({
  selector: 'app-travel',
  templateUrl: './travel.component.html',
  styleUrls: ['./travel.component.scss']
})
export class TravelComponent implements OnInit {
  geoChart: GoogleChartInterface;
  posts: Array<TripotoPost>;
  visitedCount = 0;
  seenPercent = 0;
  totalCountries = 195;

  constructor(private travelService: TravelService) {}

  ngOnInit() {
    this.getData();
  }

  async getData() {
    await this.getTripotoPosts();
    this.setGeoChart();
  }

  getTripotoPosts(): Promise<any> {
    return this.travelService
      .getTripotoPosts()
      .then(posts => {
        this.posts = posts.sort((p1, p2) => p1.order - p2.order);
        return Promise.resolve();
      });
  }

  setGeoChart() {
    this.travelService
      .getVisitedCountries()
      .then(countries => {
        let dataTable = [];
        this.visitedCount = countries.length;
        this.seenPercent = Math.ceil((this.visitedCount / this.totalCountries) * 100);

        // insert row header
        dataTable.push(['Country', 'PostCount']);
        dataTable = dataTable.concat(countries.map(country => {
          return [country.code, 0];
        }));

        this.drawChart(dataTable);
      });
  }

  private drawChart(data: Array<any>) {
    this.geoChart = {
      chartType: 'GeoChart',
      dataTable: data,
      options: {
        legend: 'none',
        domain: 'IN',
        tooltip: {
          trigger: 'none'
        },
        enableRegionInteractivity: true,
        colorAxis: {
          colors: ['#8B5FBF']
        }
      }
    };
  }
}

