import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { InstagramMedia } from '../models/instagram-media';
import { Injectable } from '@angular/core';

@Injectable()
export class InstagramService {
  private instagraGraphUrl: string;
  private authToken: string;
  private authTokenUrl: string;

  constructor(private httpClient: HttpClient) {
    this.instagraGraphUrl = 'https://graph.instagram.com';
    this.authTokenUrl = 'https://mariya-me.herokuapp.com/token.json';
  }

  async getMedia(tag?: string): Promise<Array<InstagramMedia>> {
    try {
      if (!this.authToken) {
        await this.setAuthToken();
      }

      const fields = 'caption,id,media_type,media_url,permalink,thumbnail_url,timestamp,username';
      const mediaUrl = `${this.instagraGraphUrl}/me/media?fields=${fields}&access_token=${this.authToken}&limit=200`;

      return this.httpClient
        .get(mediaUrl)
        .toPromise()
        .then((response: any) => {
          let data = response.data;

          if (data) {
            if (tag) {
              data = data.filter(media => media.caption && media.caption.indexOf(tag) >= 0);
            }
            return data.map(media => {
              const instragramMedia = new InstagramMedia();
              instragramMedia.caption = media.caption;
              instragramMedia.id = media.id;
              instragramMedia.mediaUrl = media.media_url;
              instragramMedia.mediaType = media.media_type;
              instragramMedia.permalink = media.permalink;
              instragramMedia.timestamp = media.timestamp;

              return instragramMedia;
            });
          }
        })
        .catch((err: HttpErrorResponse) => {
          console.error('Error fetching instagram media');
          return Promise.reject(err);
        });

    } catch (error) {
      console.error('Error fetching instagram auth token');
      return Promise.reject(error);
    }
  }

  setAuthToken(): Promise<any> {
    return this.httpClient
      .get(this.authTokenUrl)
      .toPromise()
      .then((data: any) => {
        this.authToken = data.token;
        return Promise.resolve();
      })
      .catch((err: HttpErrorResponse) => Promise.reject(err));
  }
}
