import { Component, OnInit } from '@angular/core';
import { BehanceService } from '../../services/behance.service';
import { DesignMedia } from '../../models/design-media';
import { BsModalService } from 'ngx-bootstrap/modal';
import { DesignViewModalComponent } from './design-view-modal.component';

@Component({
  selector: 'app-design',
  templateUrl: './design.component.html',
  styleUrls: ['./design.component.scss']
})
export class DesignComponent implements OnInit {
  designMedia: Array<DesignMedia>;

  constructor(
    private behanceService: BehanceService,
    private modalService: BsModalService
  ) {}

  ngOnInit() {
    this.behanceService
      .getMedia()
      .then(data => this.designMedia = data);
  }

  showDesign(media: DesignMedia) {
    const modalRef = this.modalService.show(DesignViewModalComponent, { backdrop: 'static' });
    const modalInstance = modalRef.content;
    console.log(modalRef.content);
    modalInstance.media = media;
  }
}
