import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DesignMedia } from '../models/design-media';
import { environment } from '../../environments/environment';

@Injectable()
export class BehanceService {

  constructor(private httpClient: HttpClient, private window: Window) {}

  getMedia(): Promise<Array<DesignMedia>> {
    const url = `${this.window.location.origin}/assets/data/behance.json`;
    return this.httpClient
      .get<Array<DesignMedia>>(url)
      .toPromise();
  }
}
