import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { TripotoPost } from '../models/tripoto-post';
import { Country } from '../models/country';

@Injectable()
export class TravelService {
  constructor(private httpClient: HttpClient, private window: Window) {}

  getTripotoPosts(): Promise<Array<TripotoPost>> {
    const url = `${this.window.location.origin}/assets/data/tripoto.json`;
    return this.httpClient.get<Array<TripotoPost>>(url).toPromise();
  }

  getVisitedCountries(): Promise<Array<Country>> {
    const url = `${this.window.location.origin}/assets/data/countries.json`;
    return this.httpClient
      .get(url)
      .toPromise()
      .then((response: any) => response.visited);
  }
}
